export default {
  name: 'ViewerComponent',
  components: {
    EditorWrapper: function EditorWrapper() {
      return import(
      /* webpackChunkName: "editor" */
      './EditorWrapper');
    }
  },
  props: {
    filename: {
      type: String,
      default: null
    },
    fileid: {
      type: Number,
      default: null
    },
    active: {
      type: Boolean,
      default: false
    },
    autofocus: {
      type: Boolean,
      default: true
    },
    shareToken: {
      type: String,
      default: null
    },
    mime: {
      type: String,
      default: null
    }
  },
  beforeMount: function beforeMount() {
    // FIXME Dirty fix to avoid recreating the component on stable16
    if (typeof this.$parent.$parent !== 'undefined' && this.$parent.$parent.onResize) {
      window.removeEventListener('resize', this.$parent.$parent.onResize);
    }
  }
};